.user-profile {
 margin: 100px 60px 200px;
 .last-active {
  margin-bottom: 66px;
 }
 .card-partner {
  justify-content: flex-start;
  &__info {
   margin-top: 120px;
   &-name {
    font-size: 70px;
    line-height: 48px;
    margin-bottom: 30px;
   }
   &-wrapper {
    margin-bottom: 70px;
   }
  }
  &__report {
   color: #a3a8bb;
   margin-left: 70px;
   cursor: pointer;
   img {
    margin-left: 5px;
    margin-bottom: 2px;
   }
  }
 }

 .ant-btn {
  padding: 21px 40px;
  img {
   margin-left: 12px;
  }
 }

}
